import React from "react";
import { Redirect, Switch, Route } from "react-router-dom";
import { StartScreen } from "../components/StartScreen";
import { Game } from "../components/Game";
import { LevelList } from "../components/LevelList";
import { Settings } from "../components/Settings";

export const Routes = () => {
  return (
    <Switch>
      <Redirect exact from="/" to="/dash_game_2.0/start" />
      <Route path="/dash_game_2.0/start" component={StartScreen} />
      <Route path="/dash_game_2.0/game" component={Game} />
      <Route path="/dash_game_2.0/settings" component={Settings} />
      <Route path="/dash_game_2.0/levels" component={LevelList} />
    </Switch>
  );
};
